export default {
	data(){
		return {
			mode: 'new',
			title: '',
			text: '',
			file: null,
			visible: '1',
			publicationDate: null
		}
	},
	computed: {
		maintitle(){
			return (this.mode === 'new') ? 'Добавление новости' : 'Редактирование новости'
		}
	},
	methods: {
		async onSubmit(){
			await this.$store.dispatch('news/ADD_FORM', {
				'title': this.title,
				'text': this.text,
				'file': this.file,
				'visible': parseInt(this.visible),
				'publicationDate' : this.publicationDate
			})
		}
	},
	created(){
		let now = new Date();
		let month = now.getMonth()+1
		let day =  now.getDate()
		month = (month < 10) ? '0'+month : month
		day = (day < 10) ? '0'+day : day
		let date = `${now.getFullYear()}-${month}-${day}`
		date+=`T${now.getHours()}:${now.getMinutes()}`
		this.publicationDate = date
	},
	mounted(){
		this.mode = this.$route.query.edit ? 'edit' : 'new'
	}
}
